<template>
    <view class="demand_freight">
        <cu-custom bgColor="" :isBack="true"><block slot="backText"></block><block slot="content">抢单</block></cu-custom>
        <view class="demand_freight_list">
            <!-- 导航栏 -->
            <scroll-view scroll-x class="bg-white nav">
                <view class="flex text-center navBox">
                    <view class="cu-item flex-sub" :class="item.id==tabCur?'things_active_color':''" v-for="(item,index) in tabData" :key="index" @tap="tabSelect" :data-id="item.id">
                        {{item.name}}
                    </view>
                </view>
            </scroll-view>
            <!-- 审核中-列表 -->
            <scroll-view v-if="tabCur == 1" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in 5" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">广州番禺大道</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">中山番禺大道</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info">
                        <view class="freight_single_info">货物名称：这里是货物名称</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: 钢筋</view>
                            <view class="freight_right_info">运输价格: ￥234</view>
                        </view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物介质：介质</view>
                            <view class="freight_right_info">损耗范围：23-123</view>
                        </view>
                        <view class="freight_single_info">重量：24吨</view>
                        <view class="freight_single_info">开始时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">结束时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">订单编号：123456223331</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-between align-center operating">
                        <view class="time">发布时间：2020.12.12 14:14</view>
                        <button class="cu-btn bg-white grabOrderBtn">取消订单</button>
                    </view>
                </view>
            </scroll-view>
            <!-- 未通过-列表 -->
            <scroll-view v-if="tabCur == 2" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in 5" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">广州番禺大道</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">中山番禺大道</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info">
                        <view class="freight_single_info">货物名称：这里是货物名称</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: 钢筋</view>
                            <view class="freight_right_info">运输价格: ￥234</view>
                        </view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物介质：介质</view>
                            <view class="freight_right_info">损耗范围：23-123</view>
                        </view>
                        <view class="freight_single_info">重量：24吨</view>
                        <view class="freight_single_info">开始时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">结束时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">订单编号：123456223331</view>
                    </view>
                    <!-- 操作区域 -->
                    <view class="flex justify-between align-center operating">
                        <view class="time">发布时间：2020.12.12 14:14</view>
                        <button class="cu-btn bg-white grabOrderBtn">修改发布</button>
                    </view>
                    <!-- 拒绝原因 -->
                    <view class="reason">原因：这里是审核没通过的原因</view>
                </view>
            </scroll-view>
            <!-- 运输中-列表 -->
            <scroll-view v-if="tabCur == 3" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in 5" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
                        <view class="start_place">
                            <view class="start_tip">装车城市</view>
                            <view class="start_value">广州番禺大道</view>
                        </view>
                        <image class="arrow_icon" src="./../../../../../static/images/global/right_lead.png" mode="widthFix" />
                        <view class="arrive_place">
                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">中山番禺大道</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info">
                        <view class="freight_single_info">货物名称：这里是货物名称</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: 钢筋</view>
                            <view class="freight_right_info">运输价格: ￥234</view>
                        </view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物介质：介质</view>
                            <view class="freight_right_info">损耗范围：23-123</view>
                        </view>
                        <view class="freight_single_info">重量：24吨</view>
                        <view class="freight_single_info">开始时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">结束时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">订单编号：123456223331</view>
                        <view class="freight_single_info order_type">订单状态：<text class="type_text">运输中</text></view>
                    </view>
                </view>
            </scroll-view>
            <!-- 已完成-列表 -->
            <scroll-view v-if="tabCur == 4" class="freight_list" scroll-y="true">
                <view class="freight_item" v-for="(item,index) in 5" :key="index">
                    <!-- 出发、到达地点 -->
                    <view class="flex justify-center align-center order_place">
