                            <view class="arrive_tip">卸车城市</view>
                            <view class="arrive_value">中山番禺大道</view>
                        </view>
                    </view>
                    <!-- 货物信息 -->
                    <view class="freight_info">
                        <view class="freight_single_info">货物名称：这里是货物名称</view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物类型: 钢筋</view>
                            <view class="freight_right_info">运输价格: ￥234</view>
                        </view>
                        <view class="things_width_full flex justify-start align-center freight_double_info">
                            <view class="freight_left_info">货物介质：介质</view>
                            <view class="freight_right_info">损耗范围：23-123</view>
                        </view>
                        <view class="freight_single_info">重量：24吨</view>
                        <view class="freight_single_info">开始时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">结束时间：2020.12.12 14:14</view>
                        <view class="freight_single_info">订单编号：123456223331</view>
                        <view class="freight_single_info order_type">订单状态：<text class="type_text">待确认</text></view>
                    </view>
                </view>
            </scroll-view>
        </view>
        <!-- 弹框组件 -->
        <popLoading :loadModal="popLoadModal" @closeModal="closeModal"></popLoading>
		<popDialog :dialogModal="popDiaModal" @closeModal="closeModal"></popDialog>
        <tipModal :tipModalObject="popTipModal" @closeModal="closeModal"></tipModal>
    </view>
</template>
<script>
let that = null;
export default {
    data() {
        return {
            // 弹框
            popLoadModal: false,
            popDiaModal: false,
            popTipModal: {
                type: false,
                tipTitle: '',
                tipContent: ''
            },
            tabCur: 1,
            scrollLeft: 0,
            tabData: [
                {
                    id: 1,
                    name: '审核中'
                },
                {
                    id: 2,
                    name: '未通过'
                },
                {
                    id: 3,
                    name: '运输中'
                },
                {
                    id: 4,
                    name: '已完成'
                }
            ]
        }
    },
    methods: {
        tabSelect(e) {
            this.tabCur = e.currentTarget.dataset.id;
            this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        that.tabCur = options.type;
    }
}
</script>

<style lang="scss" scoped>
    @import "./index";
</style>
